.search-bar {
  max-width: 400px;
  margin: 0 auto;
  position: relative;
}

input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
}

.suggestions {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.suggestions li {
  cursor: pointer;
  padding: 10px;
  border: 1px solid #ddd;
  margin-top: -1px;
  background-color: white;
  display: flex;
  align-items: center;
}

.suggestions li:hover {
  background-color: #f0f0f0;
}

.country-icon {
  width: 20px;
  height: 15px;
  margin-right: 10px;
}

.country-details {
  margin-top: 20px;
  border: 1px solid #ddd;
  padding: 20px;
  background-color: #f9f9f9;
}
